  border-color: #a9afb7;
  color: #a9afb7;
  background-color: transparent;
  cursor: unset;
}
.form--modal form button:disabled span:last-child {
  background-color: #a9afb7;
  color: #ffffff;
}
.form--modal form .form__agreement span {
  border-color: rgba(0, 0, 0, 0.3);
}
.form--modal form input[type=checkbox]:focus + span {
  border-color: #000000;
}

.form__container {
  padding-top: 120px;
  padding-bottom: 60px;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .form__container {
    padding-top: 100px;
  }
}
@media (max-width: 767px) {
  .form__container {
    padding-top: 67px;
    padding-bottom: 29px;
  }
}

.form__content {
  position: relative;
  overflow: hidden;
  border-radius: 16px;
}

.form__agreement {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 19px;
  /* Рисуем состояния */
  /* фокус на реальный чекбокс с клавиатуры */
  /* Активируем фейковый / декоративный чекбокс */
}
@media (max-width: 767px) {
  .form__agreement {
    margin-top: 20px;
  }
}
.form__agreement input[type=checkbox] {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.form__agreement span {
  position: relative;
  display: inline-block;
  min-width: 20px;
  margin-bottom: 0;
  height: 20px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 2px;
  vertical-align: sub;
  margin-right: 10px;
  transition: all 0.2s;
}
.form__agreement input[type=checkbox] + span svg {
  opacity: 0;
  transition: all 0.2s;
}
.form__agreement input[type=checkbox]:focus + span {
  border-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form__agreement input[type=checkbox]:disabled + span {
  border-color: #ffffff;
  background-color: rgba(255, 255, 255, 0);
}
.form__agreement input[type=checkbox]:disabled ~ label {
  cursor: default;
}
.form__agreement input[type=checkbox]:checked + span {
  background-color: white;
  color: #316dc2;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form__agreement input[type=checkbox]:checked + span svg {
  opacity: 1;
}
.form__agreement input[type=checkbox]:checked:focus + span {
  border-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0);
  color: #ffffff;
}
.form__agreement input[type=checkbox]:disabled:checked + span {
  border-color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a9afb7;
  color: #ffffff;
}

/* ховер на строку */
.form__agreement:hover span {
  border-color: #ffffff;
}
.form__agreement:hover input[type=checkbox]:checked + span {
  border-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0);
  color: #ffffff;
}
.form__agreement:hover input[type=checkbox]:disabled:checked + span {
  border-color: rgba(255, 255, 255, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a9afb7;
  color: #ffffff;
}

.footer {
  overflow: hidden;
}

.footer__container {
  display: grid;
  grid-template-columns: 52px auto auto;
  padding-bottom: 50px;
  gap: 27px 52px;
}
@media (max-width: 1199px) {
  .footer__container {
    gap: 21px;
    grid-template-rows: auto auto auto;
    grid-template-columns: 52px auto;
  }
}
@media (max-width: 767px) {
  .footer__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
}

.footer__home-link:hover,
.footer__home-link:focus {
  outline: none;
  opacity: 0.8;
  filter: drop-shadow(3px 3px #316dc2);
}

.footer__home-link:active {
  opacity: 1;
  filter: drop-shadow(3px 3px #316dc2);
}

.footer__nav-list {
  max-width: 750px;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  align-items: start;
  gap: 30px;
  margin: 0;
  padding: 0;
  padding-top: 17px;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .footer__nav-list {
    gap: 15px;
    padding-top: 10px;
    justify-content: end;
  }
}
@media (max-width: 767px) {
  .footer__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 30px;
    row-gap: 10px;
    padding-top: 0;
  }
}

@media (max-width: 767px) {
  .footer__nav-item {
    font-size: 14px;
    line-height: 20px;
  }
}
.footer__nav-item a:hover,
.footer__nav-item a:focus,
.footer__nav-item a:focus-visible {
  color: #316dc2;
  outline: none;
}
.footer__nav-item a:active {
  color: #275699;
}

.footer__socials-list {
  max-width: 250px;
  margin: 0;
  margin-left: auto;
  padding: 0;
  padding-top: 10px;
  list-style-type: none;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: end;
}
@media (max-width: 1199px) {
  .footer__socials-list {
    padding: 0;
    gap: 10px;
    grid-area: 2/2/-1/3;
  }
}
@media (max-width: 767px) {
  .footer__socials-list {
    order: 5;
    margin: 0;
  }
}

.footer__social-item a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  width: 36px;
  height: 36px;
  background-color: #316dc2;
  border-radius: 50%;
}
@media (max-width: 767px) {
  .footer__social-item a {
    width: 26px;
    height: 26px;
  }
}
.footer__social-item a:hover,
.footer__social-item a:focus {
  color: rgba(255, 255, 255, 0.5);
  outline: none;
}
.footer__social-item a:active {
  background-color: #275699;
  color: #ffffff;
}

.footer__copyrights {
  grid-column: span 2;
  font-size: 14px;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .footer__copyrights {
    grid-area: 3/1/4/3;
    font-size: 12px;
    line-height: 12px;
  }
}
@media (max-width: 767px) {
  .footer__copyrights {
    order: 4;
  }
}

.footer__privacy-policy {
  text-align: end;
  font-size: 14px;
  line-height: 18px;
  text-decoration: underline;
}
@media (max-width: 1199px) {
  .footer__privacy-policy {
    grid-area: 2/1/3/3;
    text-align: start;
    font-size: 12px;
    line-height: 12px;
  }
}

.footer__privacy-policy:hover,
.footer__privacy-policy:focus {
  color: #316dc2;
  outline: none;
}

.footer__privacy-policy:active {
  color: #275699;
}

